'use client';

const HeroSection: React.FC = () => {
  const categories = [
    { name: 'Tees', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/1650930e_e762.jpg', link: '/tees' },
    { name: 'Pants', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/0f25034f_c3b5.jpg', link: '/pants' },
    { name: 'Denim', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/1996bc0b_7622.jpg', link: '/denim' },
    { name: 'Shirts', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/74db686e_a247.jpg', link: '/shirts' },
    { name: 'Shorts', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/f9db9668_72d9.jpg', link: '/shorts' },
    { name: 'Bags', image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/53a91526_89ca.jpg', link: '/bags' },
  ];

  const featuredSections = [
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/debc76e3_e4aa', text: 'The Vacation Shop', button: 'Shop Now', link: 'https://www.everlane.com/collections/mens-vacation-shop' },
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/aeb3b3e0_5470.png', text: 'The Linen Shop', button: 'Shop Now', link: 'https://www.everlane.com/collections/mens-vacation-shop' },
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/081cf835_c9fb.jpg', text: 'Top Billing', button: 'Read More', link: 'https://www.everlane.com/collections/mens-vacation-shop' },
    { image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/e8869f36_088a.jpg', text: 'Special Occasion', button: 'Read More', link: 'https://www.everlane.com/collections/mens-vacation-shop' },
  ];

  const sustainabilitySection = {
    image: 'https://media.everlane.com/image/upload/c_scale,dpr_2.0,f_auto,q_auto:best:sensitive,w_auto/c_limit,w_900/v1/i/032b2cc9_0069.jpg',
    text: "Sustainability Looks Better With Receipts. Here's Ours.",
    button: 'Read Our Impact Report',
    link: 'https://www.everlane.com/collections/mens-vacation-shop',
  };

  return (
    <div>
      {/* Hero Video Section */}
      <section className="relative w-full h-screen overflow-hidden">
        <video
          className="absolute top-0 left-0 w-full h-full object-cover"
          src="/images/main.mp4"
          autoPlay
          loop
          muted
          playsInline
        />
        <div className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-30">
          <div className="text-center text-white">
            <h1 className="text-5xl md:text-6xl font-bold mb-4">Discover Minimalistic Clothing</h1>
            <p className="text-lg md:text-xl">Timeless style, sustainable fashion.</p>
          </div>
        </div>
      </section>

      {/* Shop by Category Section */}
      <section className="py-12 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4">
          <h2 className="text-2xl font-semibold uppercase mb-8">Shop by Category</h2>
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-6">
            {categories.map((category) => (
              <a
                key={category.name}
                href={category.link}
                className="flex flex-col items-center"
              >
                <img
                  src={category.image}
                  alt={category.name}
                  className="w-full h-64 object-contain mb-4"
                />
                <span className="text-sm font-medium uppercase text-gray-800">
                  Shop {category.name}
                </span>
              </a>
            ))}
          </div>
        </div>
      </section>

      {/* Featured Sections (2x2 Grid) */}
      <section className="w-full">
        <div className="grid grid-cols-1 md:grid-cols-2">
          {featuredSections.map((section, index) => (
            <a
              key={index}
              href={section.link}
              className="relative"
            >
              <img
                src={section.image}
                alt={section.text || `Featured Section ${index + 1}`}
                className="w-full h-[1200px] object-cover"
              />
              {section.text && (
                <div className="absolute bottom-6 left-6 text-white">
                  <h3 className="text-xl font-semibold uppercase">{section.text}</h3>
                  <span className="text-sm uppercase">{section.button}</span>
                </div>
              )}
            </a>
          ))}
        </div>
      </section>

      {/* Sustainability Section (Full Width) */}
      <section className="w-full">
        <a
          href={sustainabilitySection.link}
          className="relative block"
        >
          <img
            src={sustainabilitySection.image}
            alt="Sustainability"
            className="w-full h-[600px] object-cover"
          />
          <div className="absolute inset-0 flex items-center justify-center text-white text-center">
            <div>
              <h3 className="text-2xl md:text-3xl font-semibold">{sustainabilitySection.text}</h3>
              <span className="text-sm uppercase mt-2 inline-block">{sustainabilitySection.button}</span>
            </div>
          </div>
        </a>
      </section>
    </div>
  );
};

export default HeroSection;